import React from "react"
import cx from 'classnames'
import { SpinnerOverlay } from './SpinnerOverlay';

type Props = {
  type: "default" | "full";
  overlayClass: string;
  spinnerClass: string;
}

export const Spinner = ({type = 'default', overlayClass = '', spinnerClass = ''}: Props) => {
  if(type === 'default') return <div className={cx("spinner absolute z-[50] inset-1/2", spinnerClass)}></div>
  return (
    <SpinnerOverlay>
      <div className={cx("spinner absolute z-[50] inset-1/2", spinnerClass)}></div>
    </SpinnerOverlay>
  )
}